@import 'line-awesome.scss';

@font-face {
  font-family: 'Suisse Intl';
  src: url('../../fonts/suisse/intl/bold/suissebpintl-bold-webfont.eot');
  src:
    url('../../fonts/suisse/intl/bold/suissebpintl-bold-webfont.eot?#iefix') format('embedded-opentype'),
    url('../../fonts/suisse/intl/bold/suissebpintl-bold-webfont.woff2') format('woff2'),
    url('../../fonts/suisse/intl/bold/suissebpintl-bold-webfont.woff') format('woff'),
    url('../../fonts/suisse/intl/bold/suissebpintl-bold-webfont.ttf') format('truetype'),
    url('../../fonts/suisse/intl/bold/suissebpintl-bold-webfont.svg#svgFontName') format('svg');
  font-weight: 600;
  font-style: normal;
}

@font-face {
  font-family: 'Suisse Intl';
  src: url('../../fonts/suisse/intl/regular/suissebpintl-regular-webfont.eot');
  src:
    url('../../fonts/suisse/intl/regular/suissebpintl-regular-webfont.eot?#iefix') format('embedded-opentype'),
    url('../../fonts/suisse/intl/regular/suissebpintl-regular-webfont.woff2') format('woff2'),
    url('../../fonts/suisse/intl/regular/suissebpintl-regular-webfont.woff') format('woff'),
    url('../../fonts/suisse/intl/regular/suissebpintl-regular-webfont.ttf') format('truetype'),
    url('../../fonts/suisse/intl/regular/suissebpintl-regular-webfont.svg#svgFontName') format('svg');
  font-weight: 400;
  font-style: normal;
}

@font-face {
  font-family: 'Suisse Intl';
  src: url('../../fonts/suisse/intl/light/suissebpintl-light-webfont.eot');
